<template>
    <div class="px-2 py-4">
        <cropper
                :src="this.img"
                ref="cropper"
                :stencil-props="{minAspectRatio: $store.state.manipulate.minAspectRatio}"
        />
        <v-row class="px-2 mx-0">
            <v-spacer></v-spacer>
            <v-btn @click="crop" class="my-4">
                Ok
            </v-btn>
        </v-row>
    </div>
</template>

<script>
    import { Cropper } from 'vue-advanced-cropper';

    export default {
        components: {
            Cropper,
        },
        props: {
            img: {type: String, required: true}
        },
        data() {
            return {
                coordinates: {
                    width: 0,
                    height: 0,
                    left: 0,
                    top: 0,
                },
                image: null,
            };
        },
        methods: {
            crop() {
                const { coordinates, canvas, } = this.$refs.cropper.getResult();
                this.coordinates = coordinates;
                this.image = canvas.toDataURL();
                this.$store.commit(this.$store.state.manipulate.commit, this.image);
                this.$router.go(-1);
            },
        }
    };
</script>

<style lang="scss">
    .vue-advanced-cropper__background {
        border: 1px solid var(--wood);
    }
</style>